import React from 'react'
import classNames from 'classnames'
import '../App.css'

class RandomScreen extends React.Component {
  constructor(props) {
    super(props)
    this.state = { 
      selected: this.props.randomTune.selected 
    }
  }

  onClick = () => this.setState({ selected: true }, () => {
    this.props.toggleSelected(this.props.randomTune)
    setTimeout(() => {
      this.setState({ selected: false }, () => this.props.setNewRandomTune())
    }, 500)
  })

  render() {
    const { randomTune, setNewRandomTune } = this.props
    console.log('RandomScreen render()')

    if (randomTune) {
      return (
        <div className="Random-screen">
          <div className={classNames({
            "Random-tune": true,
            "selected": this.state.selected
          })}
          onClick={this.onClick}>
            <span className="Random-tune-title">
              {randomTune.title}
            </span>
            <span className="Random-tune-detail">
              {randomTune.composer} ({randomTune.year})
            </span>
          </div>
          <div className="Next-button" onClick={setNewRandomTune}>
            NEXT
          </div>
        </div>
      )
    }
    else return (
      <div className="Random-screen">
        <div className="Random-tune">
          <span className="Tune-detail">Loading random tune...</span>
        </div>
      </div>
    )
  }
}

export default RandomScreen


